@charset 'utf-8';

/* grid */
.schedule-date {
  display: none;
}

@supports (display: grid) {
  .schedule {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 0.5em;
  }

  .schedule-date {
    display: block;
  }

  .schedule-activity time {
    display: none;
  }
}

.schedule > * {
  padding: 0.6em 0;
}

.schedule-date {
  border-bottom: 0.8em solid #421b4b;
  padding-bottom: 0.1em;
}

.schedule-date.color-pink {
  border-color: #e40050;
}

.schedule-date.color-mauve {
  border-color: #d0a9d0;
}

.schedule-activity {
  border: 1px solid #b0a7ac;
  padding-left: 0.5em;
  padding-right: 0.5em;
}

.schedule-activity-education-summit {
  grid-column: 1 / 2 span;
}

.schedule-activity-interaction18-conference {
  grid-column: 3 span;
}

.schedule-activity-local-leader {
  grid-column: 2;
}

.schedule-activity-student-design-challenge {
  grid-column: 4 span;
}

.schedule-activity-awards {
  grid-column: 6 / 1 span;
}

.schedule-activity-more-link {
  display: block;
}

.schedule-activity time {
  font-weight: normal;
}

@media (max-width: 60em) {
  .schedule {
    margin-top: 1em;
    grid-template-columns: 4em 1fr 1fr;
  }

  .schedule-date {
    grid-column: 1;
    text-align: right;
    border-bottom: none;
    border-right: 0.6em solid #421b4b;
    padding-right: 0.4em;
  }

  .schedule-date-2rows {
    grid-row: 2 span;
  }

  .schedule-activity-education-summit {
    grid-row: 1 / 2 span;
    grid-column: 2;
  }

  .schedule-activity-workshop {
    grid-row: 3;
    grid-column: 2;
  }

  .schedule-activity-interaction18-conference {
    grid-row: 4 / 3 span;
    grid-column: 2;
  }

  .schedule-activity-local-leader {
    grid-row: 2;
    grid-column: 3;
  }

  .schedule-activity-student-design-challenge {
    grid-row: 3 / 4 span;
    grid-column: 3;
  }

  .schedule-activity-awards {
    grid-row: 7;
    grid-column: 2 / 2 span;
  }
}
