@charset 'utf-8';

ul,
ol {
  margin-left: 0;
  padding-left: 0;
}

ul li,
ol li {
  margin-bottom: 0.4em;
}

ul ul,
ol ol,
ul ol,
ol ul {
  margin-top: 0;
  margin-bottom: 0;
}

ul li {
  list-style: square;
}

ol {
  margin-left: -0.2em;
}

ol li {
  padding-left: 0.2em;
  list-style: decimal;
}

@media (max-width: 40em) {
  ol,
  ul {
    margin-left: 1em;
  }
}
