@charset 'utf-8';

/* header navigation */

.a11y-navigation {
  position: fixed;
  z-index: 100;
  top: 1em;
  left: 1em;
}

.a11y-navigation ul li {
  list-style: none;
}

/* header */

header {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  justify-content: space-between;
  align-items: center;
  min-height: 3vmax;
  background-color: #fff;
}

header .logo {
  width: 30vw;
  max-width: 340px;
  min-width: 200px;
}

.logo-home:hover .logo .st0 {
  cursor: pointer;
}

header nav {
  margin: 1em 0;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  margin: 0;
  padding: 0;
}

header nav ul li {
  list-style: none;
}

header nav a {
  padding: 0.2em 0.6em;
  text-decoration: none;
  color: #421b4b;
}

header nav .subnav a {
  text-transform: uppercase;
}

header nav a.active {
  color: #e40050;
}

header nav ul li.main-nav-button .button {
  margin-left: 1em;
}

.nav-toggle {
  display: none;
}

@media (min-width: 60em) {
  header nav .subnav {
    position: absolute;
    left: 0;
    bottom: -3.5em;
    display: flex;
    width: 100%;
    justify-content: center;
    font-size: 80%;
  }

  header nav .subnav a {
    border-bottom: 1px solid #b0a7ac;
    padding: 0.4em 0.8em;
    display: block;
    position: relative;
  }

  header nav .subnav li a:after {
    content: '';
    position: absolute;
    height: 3px;
    width: calc(100% - 1.6em);
    bottom: 0;
    right: 0.8em;
    background: #fff;
    transition: all 0.3s ease;
  }

  header nav .subnav li a:hover:after {
    background: #ad003d;
  }

  header nav .subnav li:first-child a {
    padding-left: 0;
  }

  header nav .subnav li:first-child a:after {
    width: calc(100% - 0.8em);
  }

  header nav .subnav li:last-child a {
    padding-right: 0;
  }

  header nav .subnav li:last-child a:after {
    width: calc(100% - 0.8em);
    right: 0;
  }
}

@media (max-width: 60em) {
  .js header nav ul li {
    display: none;
  }

  .js header nav ul li.main-nav-button {
    display: block;
  }

  .js header.mobile-open {
    box-sizing: border-box;
    transition: background 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11);
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 3em;
    height: 100vh;
    background-color: #421b4b;
    background-image:
      linear-gradient(
        to right,
        #421b4b,
        #e40050
      );
  }

  .js header.mobile-open .logo .st0,
  .js header.mobile-open .logo .st1 {
    fill: #fff;
  }

  .js header.mobile-open .logo {
    min-height: 5vmax;
    margin-top: 6vmax;
  }

  .js header.mobile-open nav ul {
    flex-direction: column;
    margin-top: 2em;
  }

  .js header.mobile-open nav ul li {
    display: block;
  }

  .js header.mobile-open nav ul li a {
    font-size: calc(1em + 2vh);
    font-size: -webkit-calc(100% + 2vh);
    color: #fff;
    margin: calc(0.2em + 1vh) 0;
    padding: 0;
    display: block;
    line-height: 1em;
  }

  .js header.mobile-open nav ul li a.active {
    opacity: 0.6;
  }

  .js header.mobile-open nav ul li a:hover {
    color: #d0a9d0;
  }

  .js header.mobile-open nav ul li.main-nav-button .button {
    margin-top: 1.5em;
    padding-left: 0;
    margin-left: 0;
  }

  .js header.mobile-open nav ul .subnav {
    display: inline-block;
    margin-top: 0;
    margin-bottom: 1.5em;
  }

  .js header.mobile-open nav ul .subnav li {
    display: inline-block;
  }

  .js header.mobile-open nav ul .subnav li a {
    font-size: calc(0.3em + 1.5vh);
    font-size: -webkit-calc(30% + 1.5vh);
    margin: calc(0.1em + 0.5vh) calc(1em + 0.5vh) calc(0.1em + 0.5vh) 0;
    padding: 0;
  }

  .js .nav-toggle {
    display: block;
    position: fixed;
    z-index: 8000;
    bottom: 3vh;
    right: -1.2em;
    padding: 0;
    line-height: 1em;
    box-shadow: 0 0 0 0.6em rgba(255, 255, 255, 1);
    border-radius: 0;
  }

  .js .nav-toggle.nav-toggle-open {
    background-color: #421b4b;
    color: #fff;
    box-shadow: 0 0 0 rgba(255, 255, 255, 0);
  }

  .js .nav-toggle span {
    display: block;
    padding: 1em 2em 1em 1em;
    font-size: 1.4em;
  }

  .js .nav-toggle svg {
    height: 0.8em;
    padding-right: 0.4em;
  }

  .js .nav-toggle .nav-toggle-close-text {
    display: none;
  }
  .js .nav-toggle.nav-toggle-open .nav-toggle-close-text { display: block; }
  .js .nav-toggle.nav-toggle-open .nav-toggle-open-text { display: none; }
}
