@charset 'utf-8';

.banner {
  text-align: center;
  padding: 0 1vmax;
}

.banner-purple {
  background: #421b4b url('../images/photos/saone_rhone.jpg') 0 0 no-repeat;
  background-size: cover;
}

.banner-purple-speakers {
  background: #421b4b url('../images/photos/speakers.jpg') center 0 no-repeat;
  background-size: cover;
}

.banner-purple-cfp {
  background: #421b4b url('../images/photos/cfp-cover.jpg') center 0 no-repeat;
  background-size: cover;
}

.banner-neutral {
  background-color: #b0a7ac;
}

.banner-half {
  min-height: 30vh;
}

.banner-full {
  min-height: 60vh;
}

.banner-link {
  transition: filter 0.3s ease-in-out;
}

.banner-link:hover {
  filter: brightness(1.3);
}

.banner .button-banner {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  padding: 1em;
}

.banner .button-banner p {
  margin-top: 2em;
  margin-bottom: 2em;
}

.banner-video {
  position: relative;
}

.banner-video .button-banner {
  padding-top: 0;
  padding-bottom: 0;
}

.banner-video iframe {
  display: none;
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 60vh;
}

.banner-video iframe.video-show {
  display: block;
}

.button-play {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-play .button-play_round {
  width: 4em;
  height: 4em;
  min-width: 50px;
  min-height: 50px;
  border-radius: 50%;
  background-color: #421b4b;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease-in-out;
}

.button-banner:hover .button-play .button-play_round {
  background-color: #e40050;
  width: 4.6em;
  height: 4.6em;
  margin-top: 2em;
}

.button-play .button-play_triangle {
  width: 0;
  height: 0;
  margin-left: 8%;
  border-top: 0.8em solid transparent;
  border-bottom: 0.8em solid transparent;
  border-left: 1.2em solid white;
  display: block;
  transition: all 0.2s ease-in-out;
}

.button-banner:hover .button-play .button-play_triangle {
  border-top-width: 1em;
  border-bottom-width: 1em;
  border-left-width: 1.5em;
}

.button-play strong {
  margin-top: 0.5em;
}
