@charset 'utf-8';

/* BUTTONS */

.button {
  border: none;
  display: inline-block;
  background-color: #e40050;
  color: #fff;
  border-radius: 3px;
  padding: 0.6em 1em;
  transition: background 0.2s ease-in-out;
  text-decoration: none;
  cursor: pointer;
}

.button:hover,
.button:focus {
  background-color: #ad003d;
  color: #fff;
}

.button-secondary {
  background-color: #421b4b;
}

.button-secondary:hover,
.button-secondary:focus {
  background-color: #290930;
  color: #d0a9d0;
}

.button-disabled,
.button-disabled:hover {
  background-color: #b0a7ac;
  color: #fff;
  cursor: default;
}

@media (max-width: 60em) {
  .button-xs-link {
    background: none;
    color: #e40050;
    text-decoration: underline;
  }

  .button-xs-link:hover,
  .button-xs-link:focus {
    background: none;
    color: #ad003d;
  }

  .button-xs-link.button-disabled,
  .button-xs-link.button-disabled:hover {
    color: #b0a7ac;
  }
}
