@charset "utf-8";

// Fonts
@font-face {
  font-family: 'graphik';
  src: url('../fonts/Graphik-Regular-Web.eot');
  src:
    url('../fonts/Graphik-Regular-Web.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Graphik-Regular-Web.woff2') format('woff2'),
    url('../fonts/Graphik-Regular-Web.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
}

@font-face {
  font-family: 'graphik-bold';
  src: url('../fonts/Graphik-Bold-Web.eot');
  src:
    url('../fonts/Graphik-Bold-Web.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Graphik-Bold-Web.woff2') format('woff2'),
    url('../fonts/Graphik-Bold-Web.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
}

html {
  font-size: 14px;
  color: #421b4b;
  font-family: graphik, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

body {
  font-size: calc(0.5em + 1vmax);
  font-size: -webkit-calc(50% + 1vmax);
}

@media (min-width: 90rem) {
  body {
    font-size: 1.5em;
  }
}

b,
strong {
  font-family: graphik-bold, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: 700;
}

p,
ul,
ol,
pre,
table,
blockquote {
  line-height: 1.43em;
  margin-top: 0;
  margin-bottom: 0;
}

p + p,
p + h2,
p + h3,
p + ul,
ul + p,
h2 + p,
h3 + ol,
h3 + ul,
h2 + ol,
h2 + ul {
  margin-top: 0.8em;
}

input,
textarea,
keygen,
select,
button {
  font-size: 100%;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}
