/* UTILITIES */
.text-white{
  color: #ffffff;
  transition: color 0.3s ease-in-out;
}
  .text-white:hover{
    color: rgba(255,255,255,0.9);
  }
.text-fade{
  color: #B0A7AC;
}
.text-xs{
  font-size:0.8em;
}
.link-no-underline{
  text-decoration: none;
}
.text-center{
  text-align: center;
}
.no-scroll{
  overflow: hidden;
}
.column-text-2{
  column-count: 2;
  column-gap: 2em;
}
@media (max-width: 30em) {
  .column-text-2{
    column-count: 1;
  }
}


/* PADDINGS AND BORDERS */
.mega-padding{
  padding: 1.6em 2em;
}
.bordered{
  border: 1px solid #B0A7AC;
}
.bordered-top{
  border-top: 1px solid #B0A7AC;
}
.bordered-bottom{
  border-bottom: 1px solid #B0A7AC;
}
.spacer-t-b{
  margin-top: 3em;
  margin-bottom: 3em;
}
.spacer-2t-b{
  margin-top: 6em;
  margin-bottom: 3em;
}
.spacer-2t-2b{
  margin-top: 6em;
  margin-bottom: 6em;
}
.spacer-t-2b{
  margin-top: 3em;
  margin-bottom: 6em;
}
.spacer-t{
  margin-top: 3em;
}
.spacer-b{
  margin-bottom: 3em;
}
.spacer-r-l{
  margin-left: 3em;
  margin-right: 3em;
}
.spacer-none{
  margin: 0em;
}


/* THEME BACKGROUND */
.content .bg-pink{
  background-color: #E40050;
  color: #ffffff !important;
}
.bg-pink a{
  color: #ffffff !important;
}
.color-pink, .content a:not(.button).color-pink{
  color: #E40050;
}

.content .bg-purple{
  background-color: #421B4B;
  color: #ffffff !important;
}
.bg-purple a{
  color: #ffffff !important;
}
.color-purple, .content a:not(.button).color-purple{
  color: #421B4B;
}

.content .bg-mauve{
  background-color: #D0A9D0;
}
.color-mauve, .content a:not(.button).color-mauve{
  color: #D0A9D0;
}

/* CONTAINER */
.container{
  max-width: 70em;
  margin-left: auto;
  margin-right: auto;
  clear: both;
}
.container-s{
  max-width: 40em;
}
.container-xs{
  max-width: 32em;
}

.content{

}
  .content img{
    max-width: 100%;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
  }
  .content .box img{
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }

/* JS class */
.js .js-collapsed{
  display: none;
}
.js .arrow-down{
  display: inline-block;
}
.js .js-collapse-active .arrow-down{
  transform: rotate(0.5turn);
  transform-origin: 0.45em 0.75em;
}


/* FORMS */
.forms{

}
.form-contest{

}
.form-contest label{
  line-height: 1.4em;
  margin-top: 1em;
  margin-bottom: 0.3em;
  display: block;
}
.form-contest .form-input{
  display: block;
  margin-bottom: 1em;
  font-size: 16px;
  width: 97%;
}
input:not(.button){
  line-height: 1.5em;
  padding: 0.3em;
}

/* A11Y */
.sr-only{
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}
.sr-show-on-focus:focus{
  z-index: 20;
  width: auto;
  min-width: 10em;
  height: auto;
  clip: auto;
  background-color: #fff;
  padding: 0.4em 1em;
  background-color: #E40050;
  color: #fff;
}
