@charset 'utf-8';

h1,
.h1 {
  font-size: calc(1.4em + 2vmax);
  font-size: -webkit-calc(140% + 2vmax);
  line-height: 1.3em;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

h2,
.h2 {
  font-size: calc(1em + 1vmax);
  font-size: -webkit-calc(100% + 1vmax);
  line-height: 1.3em;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

h3,
.h3 {
  font-size: calc(0.5em + 1vmax);
  font-size: -webkit-calc(50% + 1vmax);
  font-weight: normal;
  line-height: 1.3em;
  margin-top: 0;
  margin-bottom: 0;
}

h4,
.h4 {
  font-size: calc(0.45em + 1vmax);
  font-size: -webkit-calc(45% + 1vmax);
  font-family: graphik-bold, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: 700;
  line-height: 1.3em;
  margin-top: 0;
  margin-bottom: 0;
}

h5,
.h5 {
  font-size: calc(0.4em + 1vmax);
  font-size: -webkit-calc(40% + 1vmax);
  font-family: graphik-bold, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: 700;
  line-height: 1.3em;
  margin-top: 0;
  margin-bottom: 0;
}

h6,
.h6 {
  font-size: calc(0.35em + 1vmax);
  font-size: -webkit-calc(35% + 1vmax);
  font-weight: normal;
  line-height: 1.3em;
  margin-top: 0;
  margin-bottom: 0;
}

.content h3 { margin-bottom: 0.1em; }
.content h4 { margin-bottom: 0.1em; }
