@charset 'utf-8';

/* boxes */

.boxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 1rem -1rem;
}

.box-inner-padding {
  margin: 0.75em;
}

.boxes > * {
  margin: 1rem;
  padding-right: 0.75em;
  box-sizing: border-box;
}

.boxes.boxes-valign {
  align-items: center;
}

.boxes.boxes-nopadding > * {
  padding-right: 0;
  padding-bottom: 0;
}

.boxes.boxes-auto .box {
  flex: 1 1 auto;
}

.boxes.boxes-1 .box {
  flex: 1 0 8em;
  min-width: 8em;
}

.boxes.boxes-2 .box {
  flex: 1 0 40%;
  min-width: 25vmax;
}

.boxes.boxes-3 .box {
  flex: 1 0 20%;
  min-width: 20vmax;
}

.boxes.boxes-2-13 .box {
  flex: 1 0 20%;
  min-width: 20vmax;
}

.boxes.boxes-2-13 .box:first-child {
  flex: 1 0 40%;
  min-width: 20vmax;
}

.boxes.boxes-4 .box {
  flex: 1 0 14em;
  min-width: 14em;
}

.box-primary {
  background-color: #e40050;
  padding: 2em;
}

.box-secondary {
  background-color: #421b4b;
  padding: 2em;
}

.boxes-fun {
  flex-direction: column;
  height: 100em;
  justify-content: flex-start;
}

.boxes-fun .box {
  width: 30%;
}

@media (max-width: 60em) {
  .boxes-fun {
    height: auto;
  }

  .boxes-fun .box {
    width: 100%;
  }
}

.boxes-partners .box {
  margin-bottom: 3em;
  flex: 1 1 auto;
}

.boxes-gallery {
  flex-wrap: nowrap;
  overflow: scroll;
}

.gallery-simple {
  text-align: center;
}

.gallery-simple img {
  max-width: 700px;
  width: calc(100% - 2rem);
  margin: 1rem;
}

.boxes-logo img {
  max-width: 8em;
  margin: 1em;
}
